/* Common */
body, p, h1, h2, h3, h4, h5, h6, ul, ol, li, dl, dt, dd, table, th, td, form, fieldset, legend, input, textarea, button {margin:0;padding:0}
body, input, textarea, select, table {font-family:"돋움", Dotum, AppleGothic, sans-serif; font-size:12px;}
img, fieldset, button {border:0;}
hr {display:none;}
li {list-style:none;}
em, address {font-style:normal;}
a {text-decoration:none;}
a:hover, a:active, a:focus {text-decoration:underline;}
.blind {position:absolute; top:0; left:0; width:1px; height:1px; overflow:hidden; visibility:hidden; font-size:0; line-height:0;}

/* Layout */
#pop_header {overflow:hidden; height:37px; background:url(#{$imgUrl-service}/imgviewer/bg_header.gif) repeat-x top;}
#pop_container {overflow:hidden; position:relative; background:url(#{$imgUrl-service}/imgviewer/bg_container.gif) repeat-x top;}
#pop_footer {}

/* Header */
#pop_header h1 {float:left; padding:0 0 0 18px;}
#pop_header h1 a {display:inline-block; padding:11px 2px 8px;}
#pop_header h1 img {display:block;}

.tab_topmenu {float:right; padding:7px 20px 0 10px;}
.tab_topmenu li {display:inline; margin-left:-3px;}
.tab_topmenu li a {display:inline-block; width:78px; height:27px;}
.tab_topmenu li a.selected {background-position:0 -27px;}
.tab_topmenu .tab1 {background:url(#{$imgUrl-service}/imgviewer/top_tab1.gif) no-repeat;}
.tab_topmenu .tab2 {background:url(#{$imgUrl-service}/imgviewer/top_tab2.gif) no-repeat;}
.tab_topmenu .tab3 {background:url(#{$imgUrl-service}/imgviewer/top_tab3.gif) no-repeat;}

/* Container */
.selection {float:left; padding:6px 0 4px 11px; font-size:12px;}
.selection li {display:inline; padding:10px 23px 10px 8px;#{$ie7-hack}padding:6px 17px 10px 8px; background:url(#{$imgUrl-service}/imgviewer/bg_next_select.gif) no-repeat top right;}
.selection li.last {padding-right:2px; background:url(#{$imgUrl-service}/imgviewer/bg_next_select2.gif) no-repeat top right;}
.selection li a {display:inline-block; padding:3px 9px 3px 0; color:#555;}
.selection li a.selected {color:#333; font-weight:bold; letter-spacing:-1px; text-decoration:none;}

.layer_selection {position:absolute; top:27px; left:0; width:454px; height:135px; border:1px solid #b7bac1; background:#fff;}
.layer_selection .scroll {overflow-y:auto; overflow-x:hidden; width:454px; height:112px;#{$ie7-hack}height:110px;}
.layer_selection ul {overflow:hidden; width:100%; margin:9px 0 9px 10px; background:url(#{$imgUrl-service}/imgviewer/bg_selection.gif) repeat-y -9px;}
.layer_selection li {float:left; width:98px;#{$ie7-hack}width:106px; margin-top:-1px; padding:0 0 1px 8px; color:#bbb; line-height:20px;}
.layer_selection li a {color:#666;}
.layer_selection ul.area {overflow:hidden; width:454px; margin:9px 0 9px 17px; background:red url(#{$imgUrl-service}/imgviewer/bg_selection2.gif) repeat-y -8px;}
.layer_selection ul.area li {width:72px; padding-left:0px;}
.layer_selection ul.area strong a {color:#115b9c;}
.layer_selection p.warning {position:absolute; bottom:0; left:0; width:417px; padding:5px 0 4px 37px; color:#999; font-size:11px; letter-spacing:-1px; background:#f4f4f4 url(#{$imgUrl-service}/imgviewer/icon_warning.gif) no-repeat 17px 2px;}
.layer_selection p.warning span {color:#555;}
.layer_selection p.bottom {position:absolute; bottom:15px; left:0; width:430px; height:3px; border-left:1px solid #eee; background:#fff;}

