/* ZOOM */
#pop_zoom {}
#pop_zoom h1 {padding:10px 0 8px 24px; color:#fff; font-family:"굴림", Gulim; font-size:13px; border-bottom:1px solid #838383; background:#969696 url(#{$imgUrl-service}/imgviewer/bu_arr3.gif) no-repeat 15px 12px;}
#pop_zoom img {display:block;}
#pop_zoom dl {padding:20px 0 27px; text-align:center;}
#pop_zoom dl * {display:inline; color:#333}
#pop_zoom dt.txt {width:100%; padding-left:19px; text-align:left;}
#pop_zoom dd {font-weight:bold;}
#pop_zoom .plane_zoom {padding:0 20px;}
#pop_zoom .plane_zoom h2 {margin:30px 0 0 20px; font-size:16px; text-align:left;}
#pop_zoom .plane_zoom .img_area {height:490px; text-align:center;}
#pop_zoom .close {padding:11px 0; text-align:center; background:url(#{$imgUrl-service}/imgviewer/bg_close.gif) repeat-x top;}
#pop_zoom .close img {display:inline;}
#pop_zoom .image_zoom {overflow:auto; width:773px; height:563px;}
#pop_zoom .print_area {overflow:hidden; width:552px; height:414px; margin:7px 0 0 20px; border:1px solid #dedede; text-align:center;}
#pop_zoom .print_area.map {height:442px; margin-bottom:30px;}


@media print {
#pop_zoom .print_area {overflow:visible;}
#pop_zoom .title {display:none;}
#pop_zoom .close {display:none;}
}

